import Avatar from "./Avatar";
import Name from "./Name";
let image = {
  ava: "https://cdn.pixabay.com/photo/2015/04/23/22/00/tree-736885__480.jpg"
};

function Show() {
  return (
    <div>
      <Avatar treeImg={image.ava} />
      <Name />
    </div>
  );
}

export default Show;
